.dialog_header {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  box-sizing: border-box;
}

.dialog_info {
  display: flex;
  align-items: center;
}

.header_tools::after {
  content: "\FE19";
  font-size: 15px;
}

.dialog_container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 30px;
  box-sizing: border-box;
  border-top: 1px solid lightgray;
}

.dialog_date {
  margin: 20px auto;
  color: #999;
}

.dialog_input {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  align-items: center;
  padding: 10px 0;
  border-top: 1px solid lightgray;
}

.button_attach {
  background: none;
  border: none;
}
