.wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: space-between;
}

.back_block {
  width: 64px;
  background: #fbfbfb;
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: 2px solid #eaeaea;
}

.profile_block {
  width: 510px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.display_name {
  margin: 10px 0;
}
